<template>
  <div>
    <div class="top-style">
      <div style="display: flex;justify-content: flex-start;">
        <el-input placeholder="..."
                  prefix-icon="el-icon-search"
                  clearable
                  @clear="initEmps"
                  style="width: 350px;margin-right: 10px"
                  v-model="keyword"
                  @keydown.enter.native="initEmps"
                  :disabled="showAdvanceSearchView"></el-input>
        <el-button icon="el-icon-search"
                   type="primary"
                   @click="initEmps"
                   :disabled="showAdvanceSearchView">
          
        </el-button>

      </div>

      <div style="display: flex;justify-content: flex-end">
       
       
        <!--add -->
        <el-dialog title=""
                   :visible.sync="dialogVisible"
                   width="40%">
          <div>
            <el-form :model="emp"
                     :rules="rules"
                     ref="empForm">
              <el-row>
                <el-col :span="6">
                  <el-form-item label=":"
                                prop="posid">
                    <el-select v-model="emp.posid"
                               placeholder=""
                               style="width: 150px;">
                      <el-option v-for="item in positions"
                                 :key="item.id"
                                 :label="item.name"
                                 :value="item.id">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              
                <el-col :span="6">
                  <el-form-item label=":"
                                prop="departmentid">
                    <el-popover placement="right"
                                title=""
                                width="200"
                                trigger="manual"
                                v-model="popVisible">
                      <el-tree default-expand-all
                               :data="allDeps"
                               :props="defaultProps"
                               @node-click="handleNodeClick"></el-tree>
                      <div slot="reference"
                           style="width: 150px;display: inline-flex;font-size: 19px;border: 1px solid #dedede;height: 38px;border-radius: 5px;cursor: pointer;align-items: center;box-sizing: border-box;"
                           @click="showDepView1()">{{inputDepName}}
                      </div>
                    </el-popover>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>
          <span slot="footer"
                class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary"
                       @click="doAddEmp"> </el-button>
          </span>
        </el-dialog>
      </div>
    </div>
<el-divider></el-divider>
    <div class="content-style">
      <el-scrollbar style="height: 100%">
        <div>
          <el-table :data="emps"
                    stripe
                    border
                    max-height="685"
                    v-loading.fullscreen.lock="loading"
                    element-loading-spinner="fa fa-spinner fa-pulse fa-3x fa-fw"
                    style="width: 100%;height: 695px"
                    @selection-change="handleSelectionChange">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form 
                        label-position="left" inline class="demo-table-expand">
                  <el-form-item label=":"
                               >
                    <span>{{ props.row.gender }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.birthday }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.idcard }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.email }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.phone }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.tiptopdegree }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.specialty }}</span>
                  </el-form-item>
                  <el-form-item label=":">
                    <span>{{ props.row.school }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column prop="name"
                             align="left"
                             label=""
                             width="100">
            </el-table-column>
            <el-table-column prop="workid"
                             label=""
                             align="left"
                             width="85">
            </el-table-column>

            <el-table-column prop="department.name"
                           
                             align="left"
                             label="">
            </el-table-column>
            <el-table-column prop="position.name"
                            
                             label="">
            </el-table-column>
            <el-table-column prop="jobLevel.name"
                           
                             label="">
            </el-table-column>
            <el-table-column prop="engageform"
                            
                             align="left"
                             label="">
            </el-table-column>
            <el-table-column prop="begindate"
                            
                             align="left"
                             label="">
            </el-table-column>

            <el-table-column prop="begincontract"
                            
                             align="left"
                             label="">
            </el-table-column>
            <el-table-column prop="endcontract"
                           
                             align="left"
                             label="">
            </el-table-column>
            <el-table-column 
                             align="left"
                             label="">
              <template slot-scope="scope">
                <el-tag size="mini">{{scope.row.contractterm}}</el-tag>
                
              </template>
            </el-table-column>
            <el-table-column width="200"
                             label="">
              <template slot-scope="scope">
                <el-button type="primary"
                           @click="showEditEmpView(scope.row)"
                           style="padding: 3px">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-scrollbar>
    </div>
    <div class="bottom-style">

      <el-pagination style="margin-top: 10px"
                     background
                     @current-change="currentChange"
                     @size-change="sizeChange"
                     layout="sizes, prev, pager, next, jumper, ->, total, slot"
                     :total="total"
                     :page-sizes="[13,33,333,,3333]">
      </el-pagination>

    </div>
    <!---->

  </div>
</template>

<script>
export default {
  name: "PerSalary",
  data () {
    return {
      multipleSelection: [],
      searchValue: {
        politicid: null,
        nationid: null,
        joblevelid: null,
        posid: null,
        engageform: null,
        departmentid: null,
        beginDateScope: null
      },
      title: '',
      importDataBtnText: '',
      importDataBtnIcon: 'el-icon-upload2',
      importDataDisabled: false,
      showAdvanceSearchView: false,
      allDeps: [],
      emps: [],
      loading: false,
      popVisible: false,
      popVisible1: false,
      dialogVisible: false,
      total: 0,
      page: 1,
      keyword: '',
      size: 13,
      nations: [],
      joblevels: [],
      politicsstatus: [],
      positions: [],
      tiptopDegrees: ['', '', '', '', '', '', '', ''],
      options: [{
        value: '',
        label: ''
      }],
      inputDepName: '',
      emp: {
        id: '',
        name: "",
        gender: "",
        birthday: "1995-10-08",
        idcard: "610122199001011256",
        wedlock: "",
        nationid: 1,
        nativeplace: "",
        politicid: 13,
        email: "1224965096@qq.com",
        phone: "18565558897",
        address: "",
        departmentid: 29,
        joblevelid: 9,
        posid: 29,
        engageform: "",
        tiptopdegree: "",
        specialty: "",
        school: "",
        begindate: "2017-12-31",
        workstate: "",
        workid: "",
        contractterm: 2,
        conversiontime: "2018-03-31",
        notworkdate: null,
        begincontract: "2017-12-31",
        endcontract: "2019-12-31",
        workage: null
      },
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        gender: [{ required: true, message: '', trigger: 'blur' }],
        birthday: [{ required: true, message: '', trigger: 'blur' }],
        idcard: [{ required: true, message: '', trigger: 'blur' }, {
          pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
          message: '',
          trigger: 'blur'
        }],
        wedlock: [{ required: true, message: '', trigger: 'blur' }],
        nationid: [{ required: true, message: '', trigger: 'blur' }],
        nativePlace: [{ required: true, message: '', trigger: 'blur' }],
        politicId: [{ required: true, message: '', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }, {
          type: 'email',
          message: '',
          trigger: 'blur'
        }],
        phone: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }],
        departmentid: [{ required: true, message: '', trigger: 'blur' }],
        joblevelid: [{ required: true, message: '', trigger: 'blur' }],
        posid: [{ required: true, message: '', trigger: 'blur' }],
        engageform: [{ required: true, message: '', trigger: 'blur' }],
        tiptopdegree: [{ required: true, message: '', trigger: 'blur' }],
        specialty: [{ required: true, message: '', trigger: 'blur' }],
        school: [{ required: true, message: '', trigger: 'blur' }],
        begindate: [{ required: true, message: '', trigger: 'blur' }],
        workstate: [{ required: true, message: '', trigger: 'blur' }],
        workid: [{ required: true, message: '', trigger: 'blur' }],
        contractterm: [{ required: true, message: '', trigger: 'blur' }],
        conversiontime: [{ required: true, message: '', trigger: 'blur' }],
        notworkdate: [{ required: true, message: '', trigger: 'blur' }],
        begincontract: [{ required: true, message: '', trigger: 'blur' }],
        endcontract: [{ required: true, message: '', trigger: 'blur' }],
        workage: [{ required: true, message: '', trigger: 'blur' }],
      }
    }
  },
  mounted () {
    this.initEmps();
    this.initData();
    this.initPositions();
  },
  methods: {
    deleteMany () {
      this.$confirm('' + this.multipleSelection.length + ', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
        let ids = '?';
        this.multipleSelection.forEach(item => {
          ids += 'ids=' + item.id + '&';
        })
        this.deleteRequest("/employee/basic/many/" + ids).then(resp => {
          if (resp) {
            this.initEmps();
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: '     ',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
      });
    },
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    /**/
    searvhViewHandleNodeClick (data) {
      this.inputDepName = data.name;
      this.searchValue.departmentid = data.id;
      this.popVisible1 = !this.popVisible1
    },
    /**/
    onError () {
      this.importDataBtnText = '';
      this.importDataBtnIcon = 'el-icon-upload2';
      this.importDataDisabled = false;
    },
    /**/
    onSuccess () {
      this.importDataBtnText = '';
      this.importDataBtnIcon = 'el-icon-upload2';
      this.importDataDisabled = false;
      this.initEmps();
    },
    /**/
    beforeUpload () {
      this.importDataBtnText = '';
      this.importDataBtnIcon = 'fa fa-circle-o-notch fa-spin  fa-fw';
      this.importDataDisabled = true;
    },
    exportData () {
      window.open('/employee/basic/export', '_parent');
    },
    emptyEmp () {
      this.emp = {
        id: '',
        name: "",
        gender: "",
        birthday: "1995-10-08",
        idcard: "610122199001011256",
        wedlock: "",
        nationid: 1,
        nativeplace: "",
        politicid: 13,
        email: "1224965096@qq.com",
        phone: "18565558897",
        address: "",
        departmentid: 8,
        joblevelid: 9,
        posid: 29,
        engageform: "",
        tiptopdegree: "",
        specialty: "",
        school: "",
        begindate: "2017-12-31",
        workstate: "",
        workid: "",
        contractterm: 2,
        conversiontime: "2018-03-31",
        notworkdate: null,
        begincontract: "2017-12-31",
        endcontract: "2019-12-31",
        workage: null
      }
      this.inputDepName = '';
    },
    showEditEmpView (data) {
      this.initPositions();
      this.title = '';
      this.emp = data;
      this.inputDepName = data.department.name;
      this.dialogVisible = true;
    },
    deleteEmp (data) {
      this.$confirm('' + data.name + ', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
        this.deleteRequest("/employee/basic/" + data.id).then(resp => {
          if (resp) {
            this.initEmps();
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: '     ',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
      });
    },
    doAddEmp () {
      if (this.emp.id) {
        this.$refs['empForm'].validate(valid => {
          if (valid) {
            this.$notify.success({
              title: '',
              message: '     ...',
              showClose: false,
              offset: 100,
              duration: 2000,
              customClass: 'fontclass'
            });
            this.putRequest("/employee/basic/", this.emp).then(resp => {
              if (resp) {
                this.dialogVisible = false;
                this.initEmps();
              }
            })
          }
        });
      } else {
        this.$refs['empForm'].validate(valid => {
          if (valid) {
            this.$notify.success({
              title: '',
              message: '    ...',
              showClose: false,
              offset: 100,
              duration: 4000,
              customClass: 'fontclass'
            });
            this.postRequest("/employee/basic/", this.emp).then(resp => {
              if (resp) {
                this.dialogVisible = false;
                this.initEmps();
              }
            })
          }
        });
      }
    },
    handleNodeClick (data) {
      this.inputDepName = data.name;
      this.emp.departmentid = data.id;
      this.popVisible = !this.popVisible
    },
    /**/
    showDepView () {
      this.popVisible1 = !this.popVisible1
    },
    showDepView1 () {
      this.popVisible = !this.popVisible
    },
    /**/
    initPositions () {
      this.getRequest('/employee/basic/positions').then(resp => {
        if (resp) {
          this.positions = resp;
        }
      })
    },
    getMaxWordID () {
      this.getRequest("/employee/basic/maxWorkID").then(resp => {
        if (resp) {
          this.emp.workid = resp.obj;
        }
      })
    },
    /**/
    initData () {
      /**/
      /**/
      if (!window.sessionStorage.getItem("nations")) {
        this.getRequest('/employee/basic/nations').then(resp => {
          if (resp) {
            this.nations = resp;
            window.sessionStorage.setItem("nations", JSON.stringify(resp));
          }
        })
      } else {
        this.nations = JSON.parse(window.sessionStorage.getItem("nations"));
      }
      /**/
      if (!window.sessionStorage.getItem("joblevels")) {
        this.getRequest('/employee/basic/joblevels').then(resp => {
          if (resp) {
            this.joblevels = resp;
            window.sessionStorage.setItem("joblevels", JSON.stringify(resp));
          }
        })
      } else {
        this.joblevels = JSON.parse(window.sessionStorage.getItem("joblevels"));
      }
      /**/
      if (!window.sessionStorage.getItem("politicsstatus")) {
        this.getRequest('/employee/basic/politicsstatus').then(resp => {
          if (resp) {
            this.politicsstatus = resp;
            window.sessionStorage.setItem("politicsstatus", JSON.stringify(resp));
          }
        })
      } else {
        this.politicsstatus = JSON.parse(window.sessionStorage.getItem("politicsstatus"));
      }
      /**/
      if (!window.sessionStorage.getItem("deps")) {
        this.getRequest('/employee/basic/deps').then(resp => {
          if (resp) {
            this.allDeps = resp;
            window.sessionStorage.setItem("deps", JSON.stringify(resp));
          }
        })
      } else {
        this.allDeps = JSON.parse(window.sessionStorage.getItem("deps"));
      }
    },
    /**/
    sizeChange (currentSize) {
      this.size = currentSize;
      this.initEmps();
    },
    currentChange (currentPage) {
      this.page = currentPage;
      this.initEmps();
    },
    showAddEmpView () {
      this.emptyEmp();
      this.title = '';
      this.getMaxWordID();
      this.dialogVisible = true;
    },
    /**/
    initEmps (type) {
      this.loading = true;
      let url = '/employee/basic/?page=' + this.page + '&size=' + this.size;
      if (type && type == 'advanced') {
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 2000,
          customClass: 'fontclass'
        });
        if (this.searchValue.politicid) {
          url += '&politicid=' + this.searchValue.politicid;
        }
        if (this.searchValue.nationid) {
          url += '&nationid=' + this.searchValue.nationid;
        }
        if (this.searchValue.joblevelid) {
          url += '&joblevelid=' + this.searchValue.joblevelid;
        }
        if (this.searchValue.posid) {
          url += '&posid=' + this.searchValue.posid;
        }
        if (this.searchValue.engageform) {
          url += '&engageform=' + this.searchValue.engageform;
        }
        if (this.searchValue.departmentid) {
          url += '&departmentid=' + this.searchValue.departmentid;
        }
        if (this.searchValue.beginDateScope) {
          url += '&beginDateScope=' + this.searchValue.beginDateScope;
        }
      } else if (this.keyword) {
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 2000,
          customClass: 'fontclass'
        });
        url += "&name=" + this.keyword;
      }
      /* */
      this.getRequest(url).then(resp => {
        this.loading = false;
        if (resp) {
          this.emps = resp.data;
          /**/
          this.total = resp.total;
          this.searchValue = [];
          this.keyword = '';
        }
      });
    }
  }
}
</script>

<style  >
.top-style {
  /**/
  display: flex;
  /**/
  justify-content: space-between;
}

.content-style {
  margin-top: 10px;
}

.content-style .el-scrollbar__wrap {
  overflow: scroll;
}

.bottom-style {
  display: flex;
  justify-content: space-between;
}

.fontclasssys {
  font-family: ;
}

.loadingstyle {
  font-family: ;
  font-size: 25px;
}
/*  */
/*  */
.slide-fade-enter-active {
  transition: all 0.8s ease;
}

.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */
 {
  transform: translateX(10px);
  opacity: 0;
}
.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>

